.navbar a
{
    font-size: 3em;
}
.logo
{
    display: flex;
    align-content: center;
    align-items: center;
}
.logo > img
{
    max-height: 120px;
}
@media (min-width : 992px)
{
    .logo > img
    {
        max-height: 60px;
    }
    .navbar a
    {
        font-size: 1em;
    }
}
